import React from "react";
import { useForm } from "react-hook-form";
import "leaflet/dist/leaflet.css";
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  Tooltip,
  CircleMarker,
} from "react-leaflet";

const center = [59.923615, 30.371253];

const Form = () => {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();
  const onSubmit = (data) => console.log(data);
  return (
    <div className="container">
      <div className="row row-cols-1 row-cols-md-3 row-cols-sm-2 text-center justify-content-center">
        <form id="form" onSubmit={handleSubmit(onSubmit)}>
          <h1>Заполните заявку на обратную связь</h1>
          <div className="input-group mb-3">
            <input
              defaultValue="Фамилия"
              {...register("surname", { maxLength: 50 })}
              type="text"
              className="form-control"
              placeholder="Фамилия"
            />
          </div>
          <div className="input-group mb-3">
            <input
              defaultValue="Имя"
              {...register("name", { maxLength: 50 })}
              type="text"
              className="form-control"
              placeholder="Имя"
            />
          </div>
          <div className="input-group mb-3">
            <input
              defaultValue="Отчество"
              {...register("fname", { maxLength: 50 })}
              type="text"
              className="form-control"
              placeholder="Отчество"
            />
          </div>
          <div className="input-group mb-3">
            <input
              defaultValue="E-mail"
              {...register("email", { maxLength: 50 })}
              type="text"
              className="form-control"
              placeholder="Почта"
            />
          </div>
          {errors.exampleRequired && <span>This field is required</span>}
          <input type="submit" />
        </form>
      </div>
      <MapContainer
        center={center}
        zoom={10}
        style={{ width: "100", height: "400px" }}
      >
        <TileLayer
          url="https://api.maptiler.com/maps/basic-v2/{z}/{x}/{y}.png?key=JHGJEdqtCO4uWgLeAkHk"
          attribution='<a href="https://www.maptiler.com/copyright/" target="_blank">&copy; MapTiler</a> <a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>'
        />
        <CircleMarker
          center={center}
          pathOptions={{ color: "black" }}
          radius={10}
        />
        <Marker position={center}>
          <Popup>Мы находимся здесь.</Popup>
          <Tooltip>При наведении</Tooltip>
        </Marker>
      </MapContainer>
    </div>
  );
};

export default Form;
